import React from 'react';

class ProductionCard extends React.Component {
    constructor({ id, img, name, text }){
        super({ id, img, name, text })

        this.state = {
            hidden: false
        }
    }

    changeValue = () => {
        this.setState({hidden: !this.state.hidden})
    }

render(){
    const { img, name, text } = this.props;
    const { hidden } = this.state;
    return (     
        <div className="production-card"
            onClick={this.changeValue}>
            <div 
                className='production-card__image'
                style={{
                    backgroundImage: `url(${img})`
                }}
            />
            <h1 className='production-card__title'>
                {name}
            </h1>

            {console.log(hidden)}
            { hidden ? (
                <div className='production-card__slide'>
                    <div className="production-card__slide-description">
                        {text}
                    </div>
                    <button className='production-card__slide-button'>
                        Preist na web
                    </button>
                </div>
            ) : null}
        </div>
                
    )
}
}


export default ProductionCard;